import { Component, OnInit } from '@angular/core';
import { LeaveService } from '../services/leave.service';
import { MatDialog } from '@angular/material';
import { LeaveComponent } from '../leave.component';

@Component({
  selector: 'app-leave-list',
  templateUrl: './leave-list.component.html',
  styleUrls: ['./leave-list.component.css']
})
export class LeaveListComponent implements OnInit {

  isPopupOpened: true;
  constructor(private dialog: MatDialog , private _leaveService: LeaveService) { }

  ngOnInit() {
  }
  get leaveList() {
    return this._leaveService.getLeave();
  }
  editLeave(id: number){

    this.isPopupOpened = true;
    const tax = this._leaveService.getLeave().find(c => c.LeaveId === id);
    const dialogRef = this.dialog.open(LeaveComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
     data: tax
    });
  }

  deleteLeave(id: number) {
    this._leaveService.deleteLeave(id);
  }

}
