import { Component, OnInit } from '@angular/core';
import { LoanComponent } from '../loan.component';
import { MatDialog, MatTableDataSource, MatDialogConfig } from '@angular/material';
import { LoanService } from '../services/loan.service';

@Component({
  selector: 'app-loan-list',
  templateUrl: './loan-list.component.html',
  styleUrls: ['./loan-list.component.css']
})
export class LoanListComponent implements OnInit {
  isPopupOpened: true;
  displayedColumns: string[] = ['addtaxid', 'taxname', 'taxvalue', 'status', 'actions'];
  dataSource = new MatTableDataSource(this._loanService._loanDetails);
  constructor(private dialog: MatDialog , private _loanService: LoanService) { }

  ngOnInit() {
   
  }
  get loanList() {
    return this._loanService.getLoan();
  }

  addUser() {
    const dialogConfig = new MatDialogConfig();
    this.isPopupOpened = true;
    const dialogRef = this.dialog.open(LoanComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
    });
    dialogConfig.width = '60px';
   
  }

  editUser(id: number) {
    this.isPopupOpened = true;
    const tax = this._loanService.getLoan().find(c => c.LoanId === id);
    const dialogRef = this.dialog.open(LoanComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
     data: tax
    });
  }

  deleteLoan(id: number) {
    this._loanService.deleteLoan(id);
  }
}
